<script>
    import {firebase, db, googleProvider} from './../firebase.js';
    import {FirebaseApp, User, Doc, Collection} from 'sveltefire';
    import { createEventDispatcher } from 'svelte';

    export let region;
    export let city;

    let reviewText;

    const dispatch = createEventDispatcher();

    function cancelOrder(ref, order) {

        db.collection(ref).doc(order.id)
        .delete().then(function() {
            alert(" !");
        }).catch(function(error) {
            console.error("Error removing document: ", error);
        });
    }

    function playSoundBeBe() {
        let audio = new Audio('./sounds/car_horn_beep_x3_short.mp3');
        audio.play();
    }

    function findDriverLocation(uid) {
        db.collection('taxiDrivers').doc(uid)
        .get({source: 'server'})
        .then(function(doc) {
            let coors = doc.data().location;
            dispatch('findDriverLocation', {
                location: coors
            });
        }).catch(function(error) {
            console.log("Error getting document:", error);
        });
    }

    function acceptOrder(ref, order) {
        db.collection(ref).doc(order.id).update({
            onway: true
        })
        .then(function() {
            alert("  !");
        })
        .catch(function(error) {
            // The document probably doesn't exist.
            console.error("Error updating document: ", error);
        });
    }

    function setReview(rating, ref, order) {
        db.collection(ref).doc(order.id).update({
            rating: rating
        })
        .then(function() {
            db.collection(`taxiDrivers/${order.setDriver}/ratings/`).add({
                date: firebase.firestore.FieldValue.serverTimestamp(),
                rating: rating,
                review: reviewText
            })
            .then(function() {

                alert("  !");
            })
            .catch(function(error) {
                // The document probably doesn't exist.
                console.error("Error updating document: ", error);
            });
        })
        .catch(function(error) {
            // The document probably doesn't exist.
            console.error("Error updating document: ", error);
        });
    }

</script>

<FirebaseApp {firebase}>
    <User let:user>
        <Collection 
        path={`taxiOrders/region/${region}/city/${city}`} 
        query={ (ref) => ref.where("clientUID", "==", user.uid) } 
        let:data={orders} 
        let:ref>

            <div class="taxi-orders-container z-0 fixed ml-12 mr-2">
                <div class="p-2 bg-gray-100 rounded-lg shadow-lg w-full">

                    <p class="font-semibold text-center">  : </p> 
                    <hr>

                    <!-- ORDERS -->
                    <div class="h-80 overflow-y-auto">
                        <ul>

                            {#each orders as order}
                            {#if order.payed != true}

                            <!-- ORDER -->
                            <li>

                            <div class="mt-2 text-sm">

                                <!-- SET DRIVER -->
                                {#if order.setDriver && order.onway != true}
                                    <Doc path={`taxiDrivers/${order.setDriver}`} let:data on:data={playSoundBeBe}>
                                        <div>
                                            <p class="font-semibold text-base text-green-700">
                                            <span class=" font-normal text-gray-800">   : </span> 
                                            {data.name} {data.lastName}
                                            </p>

                                            <p class="font-semibold text-base text-orange-600"> 
                                            <span class=" font-normal text-gray-800">    : </span>
                                            {data.carBrand} {data.carNumber}
                                            </p>

                                            <div class="mt-2 content-center">
                                                <img class="w-full h-32 object-cover object-none object-center" src="{data.driverAuto.frontside}" alt="auto">
                                            </div>

                                            <!-- Driver buttons -->
                                            <div class="mt-2 text-center">        
                                                
                                                <button class="order-btn bg-teal-400" on:click={findDriverLocation(order.setDriver)}>
                                                    <img class="w-8 h-8" src="./icons/place.svg" alt="placeMarker">
                                                </button>

                                                <button class="order-btn ml-4 bg-green-400">
                                                    <a href="tel:{data.phone}" alt="phone">
                                                        <img class="w-8 h-8" src="./icons/phone.svg" alt="phone">
                                                    </a>
                                                </button>
                                            </div>
                                        </div>
                                    </Doc>
                                {/if}

                                <!-- driver arrived -->
                                {#if order.driverArrived && order.onway != true}
                                    <p class="mt-4 font-semibold text-orange-600 text-lg" on:load={playSoundBeBe()}>
                                        !
                                    </p>
                                {/if}

                                <!-- ORDER DETAILS -->
                                <div class="mt-2">
                                    <p>
                                    <span class="font-semibold"> : </span>  {order.address} 
                                    </p>
                                    <p>
                                    <span class="font-semibold"> : </span> {order.routes} 
                                    </p>
                                    <p>
                                    <span class="font-semibold"> : </span> {order.desc} 
                                    </p>
                                    <p>
                                    <span class="font-semibold text-orange-600"> :  {order.price} .</span>
                                    </p>
                                </div>

                            </div>
                            <!-- BUTTONS -->
                            <div class="mt-2 mb-2 text-center">

                                {#if order.driverArrived && order.onway != true}
                                    <button class="px-2 text-white bg-green-600 shadow-lg" on:click={acceptOrder(ref.path, order)}>
                                        
                                    </button>
                                {/if}
                                
                                {#if order.driverArrived != true}
                                <button class="px-2 text-white bg-red-600 shadow-lg" on:click={cancelOrder(ref.path, order)}>
                                     
                                </button>
                                {/if}
                            </div>
                            <hr>
                            <!-- RATING & REVIEW -->
                            <div class="my-2 text-center">
                                {#if order.completed && order.rating == undefined}
                                <p class=" font-semibold text-pink-500 text-center text-lg"> :</p>

                                <input bind:value={reviewText} type="text" placeholder="  " class="my-1 bg-white">

                                <button on:click={()=> setReview(10, ref.path, order)} class="review-btn bg-purple-500">
                                
                                </button>
                                <button on:click={()=> setReview(9, ref.path, order)} class="review-btn bg-blue-500">
                                
                                </button>
                                <button on:click={()=> setReview(8, ref.path, order)} class="review-btn bg-teal-500">
                                
                                </button>
                                <button on:click={()=> setReview(7, ref.path, order)} class="review-btn bg-green-500">
                                
                                </button>
                                <button on:click={()=> setReview(6, ref.path, order)} class="review-btn bg-green-500">
                                
                                </button>
                                <button on:click={()=> setReview(5, ref.path, order)} class="review-btn bg-yellow-500">
                                
                                </button>
                                <button on:click={()=> setReview(4, ref.path, order)} class="review-btn bg-orange-500">
                                 
                                </button>
                                <button on:click={()=> setReview(3, ref.path, order)} class="review-btn bg-red-500">
                                
                                </button>
                                <button on:click={()=> setReview(2, ref.path, order)} class="review-btn bg-red-500">
                                 
                                </button>
                                <button on:click={()=> setReview(1, ref.path, order)} class="review-btn bg-red-500">
                                
                                </button>
                                {/if}
                                <hr>
                            </div>
                            </li>
                            {/if}
                            {/each}
                        </ul>
                    </div>
                </div>
            </div>

            <div slot="loading">
                <div class="p-2 ml-20 rounded-lg bg-gray-100 shadow-lg">
                    ...
                </div>
            </div>
 
            <div slot="fallback">
                <div class="p-2 ml-20 rounded-lg bg-gray-100 shadow-lg">
                      ...
                </div>
            </div>

        </Collection>
    </User>
</FirebaseApp>

<style>.order-btn {
  margin-left: 0.5rem;
  padding: 0.5rem;
  border-radius: 9999px;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  outline: 0;
}

.review-btn {
  margin-top: 0.5rem;
  margin-left: 0.5rem;
  padding: 0.5rem;
  color: #fff;
  border-radius: 9999px;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  outline: 0;
}

/*# sourceMappingURL=data:application/json;base64, */</style>