<script>
    import {firebase, db, storage, googleProvider} from './../firebase.js';
    import {FirebaseApp, User, Doc, Collection, StorageRef} from 'sveltefire';

    let storageRef = storage.ref();
    let taxiDriver = {
        registered: firebase.firestore.FieldValue.serverTimestamp(),
        rating: 5
    }

    // taxi driver registration
    function taxiDriverRegistration(uid) {
        db.collection('taxiDrivers').doc(uid).set(taxiDriver)
        .then(function() {
            console.log("Document successfully written!");
        })
        .catch(function(error) {
            console.error("Error writing document: ", error);
        });
    }

    // Upload driver License
    function driverLicenseUpload(e, uid) {
        e.stopPropagation();
        e.preventDefault();
        let inputId;
        if (e.target.id == 'driver-license-frontside') {
            inputId = "driverLicense.frontside"
        }
        if (e.target.id == 'driver-license-reverseside') {
            inputId = "driverLicense.reverseside"
        }
        let file = e.target.files[0];

        let metadata = {
            'contentType': file.type
        };

        storageRef.child(`users/${uid}/taxiDriver/driverLicense/${file.name}`)
        .put(file, metadata)
        .then(function(snapshot) {
            console.log('Uploaded', snapshot.totalBytes, 'bytes.');
            console.log('File metadata:', snapshot.metadata);
            // Let's get a download URL for the file.
            snapshot.ref.getDownloadURL().then(function(url) {
                console.log('File available at', url);

                db.collection('taxiDrivers').doc(uid)
                .update({
                    [inputId]: url
                })
                .then(function() {
                    console.log("Document successfully updated!");
                })
                .catch(function(error) {
                    // The document probably doesn't exist.
                    console.error("Error updating document: ", error);
                });
            });
        })
        .catch(function(error) {
            // [START onfailure]
            console.error('Upload failed:', error);
            // [END onfailure]
        });
    }

    // Upload driver auto
    function driverAutoUpload(e, uid) {
        e.stopPropagation();
        e.preventDefault();
        let inputId;
        if (e.target.id == 'driver-auto-frontside') {
            inputId = "driverAuto.frontside"
        }
        if (e.target.id == 'driver-auto-reverseside') {
            inputId = "driverAuto.reverseside"
        }
        let file = e.target.files[0];

        let metadata = {
            'contentType': file.type
        };

        storageRef.child(`users/${uid}/taxiDriver/driverAuto/${file.name}`)
        .put(file, metadata)
        .then(function(snapshot) {
            console.log('Uploaded', snapshot.totalBytes, 'bytes.');
            console.log('File metadata:', snapshot.metadata);
            // Let's get a download URL for the file.
            snapshot.ref.getDownloadURL().then(function(url) {
                console.log('File available at', url);

                db.collection('taxiDrivers').doc(uid)
                .update({
                    [inputId]: url
                })
                .then(function() {
                    console.log("Document successfully updated!");
                })
                .catch(function(error) {
                    // The document probably doesn't exist.
                    console.error("Error updating document: ", error);
                });
            });
        })
        .catch(function(error) {
            // [START onfailure]
            console.error('Upload failed:', error);
            // [END onfailure]
        });
    }

</script>


<FirebaseApp {firebase}>
    <User let:user>

        <div slot="signed-out" class="mt-20">
            <p>    </p>
        </div>

        <div class="profile-container mt-20">
            <Doc path={`users/${user.uid}`} let:data>

                <div class="profile-card">

                    <h3> </h3>

                    <p> : {data.username}</p>
                    <p>: {data.balance}</p>

                </div>
            </Doc>

            <!-- Taxi driver registration-->
            <Doc path={`taxiDrivers/${user.uid}`} let:data let:ref={userRef}>

                <!-- If not registered -->
                <div slot="fallback" class="profile-card">

                    <h3>  </h3>
                    <p> </p>
                    <div class="taxi-driver-registration">
                    <input type="text" placeholder="" bind:value={taxiDriver.name}>
                    <input type="text" placeholder="" bind:value={taxiDriver.secondName}>
                    <input type="text" placeholder="" bind:value={taxiDriver.lastName}>
                    <input type="text" placeholder=" " bind:value={taxiDriver.phone}>
                    <input type="text" placeholder=" " bind:value={taxiDriver.carBrand}>
                    <input type="text" placeholder=". " bind:value={taxiDriver.carNumber}>
                    <button class="btn bg-orange-500 block mt-2" on:click={taxiDriverRegistration(user.uid)}> </button>
                    </div>
                </div>

                <!-- Taxi driver documents-->
                <div class="profile-card">

                    <h3>  </h3>
                    <p>
                    ...: {data.secondName} {data.name} {data.lastName}
                    </p>
                    <p>
                     : {data.phone}
                    </p>
                    <p>
                       : {data.carBrand} {data.carNumber}
                    </p>
                    <p>
                    : {data.rating}
                    </p>
                    <br>

                    <p>  : </p>

                    <div class="image-upload">
                       {#if data.driverLicense != undefined && data.driverLicense.frontside != undefined}

                       <img class="img-up" src={data.driverLicense.frontside} alt="driverLicense"/>

                       {:else}
                        <label for="driver-license-frontside">
                                    
                            <p> </p>
                            <img src="./icons/add_photo_teal.svg" alt="add_photo" class="img-icon">
                        </label>
                        <input type="file" id="driver-license-frontside" name="file" on:change={(e) => driverLicenseUpload(e, user.uid)}/>
                        {/if}   
                    </div>

                    <div class="image-upload">
                       {#if data.driverLicense != undefined && data.driverLicense.reverseside != undefined}

                       <img class="img-up" src={data.driverLicense.reverseside} alt="driverLicense"/>

                       {:else}
                        <label for="driver-license-reverseside">
                                    
                            <p> </p>
                            <img src="./icons/add_photo_teal.svg" alt="add_photo" class="img-icon">
                        </label>
                        <input type="file" id="driver-license-reverseside" name="file" on:change={(e) => driverLicenseUpload(e, user.uid)}/>
                        {/if}   
                    </div>

                    <p> :</p>
                    
                    <div class="image-upload">
                       {#if data.driverAuto != undefined && data.driverAuto.frontside != undefined}

                       <img class="img-up" src={data.driverAuto.frontside} alt="driverAuto"/>

                       {:else}
                        <label for="driver-auto-frontside">
                                    
                            <p>   . </p>
                            <img src="./icons/add_photo_teal.svg" alt="add_photo" class="img-icon">
                        </label>
                        <input type="file" id="driver-auto-frontside" name="file" on:change={(e) => driverAutoUpload(e, user.uid)}/>
                        {/if}   
                    </div>

                    <div class="image-upload">
                       {#if data.driverAuto != undefined && data.driverAuto.reverseside != undefined}

                       <img class="img-up" src={data.driverAuto.reverseside} alt="driverAuto"/>

                       {:else}
                        <label for="driver-auto-reverseside">
                                    
                            <p>    ()</p>
                            <img src="./icons/add_photo_teal.svg" alt="add_photo" class="img-icon">
                        </label>
                        <input type="file" id="driver-auto-reverseside" name="file" on:change={(e) => driverAutoUpload(e, user.uid)}/>
                        {/if}   
                    </div>

                </div>
                
            </Doc>

                <div class="profile-card">
                    <h3>  </h3>
                    <p> ...</p>
                </div>

            
        </div>
        
    </User>
</FirebaseApp>


<style>.profile-card {
  padding: 0.5rem;
  margin-top: 1rem;
  background-color: #edf2f7;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.profile-card > h3 {
  font-weight: 600;
  font-size: 1.125rem;
}

.profile-card > p {
  color: #2d3748;
}

.taxi-driver-registration > input {
  padding: 0.25rem;
  margin-top: 0.25rem;
  background-color: #fff;
  border-radius: 0.25rem;
}

.img-icon {
  padding: 0.5rem;
  width: 5rem;
  height: 5rem;
  text-align: center;
  cursor: pointer;
}

.image-upload {
  padding: 0.5rem;
  margin-top: 1rem;
  width: 16rem;
  height: 10rem;
  background-color: #f7fafc;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  text-align: center;
  overflow: hidden;
}

.img-up{
  height: 10rem;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.image-upload > input
{
  display: none;
}

.btn {
  padding: 0.5rem;
  border-radius: 0.25rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  color: #f7fafc;
}

