        }, 3000)
    }

    // Set user location marker
    function setUserLocationMarker(e) {
        if (userLocationMarker == null || undefined) {
            userLocationMarker = L.marker(e.latlng, {icon: placeholderIcon}).addTo(map).bindPopup(" !");
            userLocation.latlng = e.latlng;
        }
    }

    // Target user location
    function targetUserLocation(e) {
        userLocationMarker.setLatLng(e.latlng);
        userLocation = {
