                region: region,
                district: district,
                city: city
            }
        console.log('userlocation', userLocation)
    }

    let watchUserLoc = false;
    // Watch user location
    function watchUserLocation() {
        if (!watchUserLoc) {
            watchUserLoc = true;
            map.locate({watch: true, setView: true, enableHighAccuracy: true});

            alertBoxCall('   .')
        } else {
            watchUserLoc = false;
            map.stopLocate();

            alertBoxCall('   .')
        }
    }

