    // Get MAP info
    function getMapInfo() {

        // Get user account info
        console.log('user id: ', getUser.uid)

        let userRef = db.collection('users');

        let accountInfo = userRef.where('uid', '==', getUser.uid);

        accountInfo.get().then(function(querySnapshot) {
            querySnapshot.forEach(function(doc) {
                
                getUserAccount = doc.data();
                console.log('mapRating', getUserAccount.mapRating);
            });
        });

        // Icon collections
        let policeCarRef = db.collection(`icons/policeCar/${region}`);
        let speedCameraRef = db.collection(`icons/speedCamera/${region}`);
        let DTPRef = db.collection(`icons/DTP/${region}`);
        let roadConstructionRef = db.collection(`icons/roadConstruction/${region}`);

        let policeCarOptions = {
            dbRef: policeCarRef,
            layer: policeCarLayer,
            icon: policeCarIcon
        }

        let speedCameraOptions = {
            dbRef: speedCameraRef,
            layer: policeCarLayer,
            icon: speedCameraIcon
        }

        let DTPOptions = {
            dbRef: DTPRef,
            layer: roadProblemLayer,
            icon: dtpIcon
        }

        let roadConstructionOptions = {
            dbRef: roadConstructionRef,
            layer: roadProblemLayer,
            icon: roadConstructionIcon
        }

        let iconCollection = [policeCarOptions, speedCameraOptions, DTPOptions, roadConstructionOptions];

        iconCollection.forEach(i=> {

            i.dbRef.get()
            .then(function(querySnapshot) {
                querySnapshot.forEach(function(doc) {
                    
                    let mark = doc.data();
                    let mDate = mark.added.toDate();
                    let today = new Date();
                    let timeLength = today.getTime() - mDate.getTime();
    
                    if(mark.actual >= 0.1 && timeLength <= 180000000) {
                        let date = mDate.toLocaleString('ru',
                            {
                                day: 'numeric',
                                month: 'long',
                                hour: '2-digit',
                                minute: '2-digit',
                            });
                        let marker = L.marker(mark.location, {
                            icon: i.icon,
                            opacity: mark.actual,
                            alt: doc.id,
                            title: doc.ref.parent.path
                            }
                        ).bindPopup(`<p>  ${date} </p>`);
    
                        marker.on('click', contextMenuDialogOpen);
    
                        i.layer.addLayer(marker);
    
                    }
    
                });
            })
            .catch(function(error) {
                console.log("Error getting documents: ", error);
            });
        })
    }

    // Context menu Dialog
    function contextMenuDialogOpen(e) {
        clickedMarkerId = e.target.options.alt;
        clickedMarkerPath = e.target.options.title;
        console.log('clickedMarkerId', clickedMarkerId);
        console.log('clickedMarkerPath', clickedMarkerPath);
        contextMenuDialogVisible = true;
        contextMenuDialog = L.marker(e.latlng, {icon: dialogDiv, zIndexOffset: 800}).addTo(map);
        contextMenuDialog.on('click', contextMenuDialogClose);
    }

    function contextMenuDialogClose(e) {
        e.target.remove();
    }

    function markerTrue() {

        let markerRef = db.collection(`${clickedMarkerPath}`).doc(`${clickedMarkerId}`);

        markerRef.get().then(function(doc) {
            if (doc.exists) {
                let data = doc.data();
                let voted = data.votes.some(el=>{
                    return el === getUser.uid
                });
                if (!voted) {
                    markerRef.update({
                        actual: firebase.firestore.FieldValue.increment(0.11),
                        votes: firebase.firestore.FieldValue.arrayUnion(getUser.uid)
                    })
                    .then(function() {
                        console.log("Document successfully updated!");
                    })
                    .catch(function(error) {
                        // The document probably doesn't exist.
                        console.error("Error updating document: ", error);
                    });
                } else {
                    alert("  !")
                }
            } else {
                // doc.data() will be undefined in this case
                alert("   !");
            }
        }).catch(function(error) {
            console.log("Error getting document:", error);
        });

    }

    function markerFalse() {

        let markerRef = db.collection(`${clickedMarkerPath}`).doc(`${clickedMarkerId}`);

        markerRef.get().then(function(doc) {
            if (doc.exists) {
                let data = doc.data();
                let voted = data.votes.some(el=>{
                    return el === getUser.uid
                });
                if (!voted) {
                    markerRef.update({
                        actual: firebase.firestore.FieldValue.increment(-0.11),
                        votes: firebase.firestore.FieldValue.arrayUnion(getUser.uid)
                    })
                    .then(function() {
                        console.log("Document successfully updated!");
                    })
                    .catch(function(error) {
                        // The document probably doesn't exist.
                        console.error("Error updating document: ", error);
                    });
                } else {
                    alert("  !")
