<div id="main" class="relative">

    <!-- MAP  -->
    <div id="map" style="min-height: 550px;" class="z-0 w-full h-screen">
    </div>

    <!-- CONTEXT MENU -->
    <div class:contextMenuVisible id="context-menu" class="hidden context-menu">
        <p class="text-center font-semibold text-base text-red-600"> ?</p>
        <div class="context-menu-item">
            <button class="context-menu-button bg-red-500" on:click={addPoliceCarIcon}> </button>
        </div>
        <div class="context-menu-item">
            <button class="context-menu-button bg-orange-500" on:click={addSpeedCameraIcon}>  </button>
        </div>
        <div class="context-menu-item">
            <button class="context-menu-button" on:click={addRoadConstructionIcon}> </button>
        </div>
        <div class="context-menu-item">
            <button class="context-menu-button" on:click={addDTPIcon}></button>
        </div>
        <div class="context-menu-item">
            <button class="context-menu-button bg-green-500" on:click={getPlaceInfo}> </button>
        </div>
        <div class="context-menu-item">
            <button class="context-menu-button bg-yellow-500" on:click={callTaxi}>  </button>
        </div>
        <div class="context-menu-item">
            <button class="context-menu-button text-gray-100 bg-gray-800" on:click={()=> contextMenu.fire('click')}></button>
        </div>
    </div>

    <!-- CONTEXT MENU DIALOG -->
    <div class:contextMenuDialogVisible id="context-menu-dialog" class="hidden context-menu-dialog">
        <div class="mt-2 ml-1">
            <button class="p-3 bg-green-600 rounded-lg text-white text-base" on:click={markerTrue}> </button>
            <button class="p-3 ml-1 bg-red-600 rounded-lg text-white text-base" on:click={markerFalse}></button>
        </div>
        <div class="mt-3 text-center">
            <button class="p-3 px-8 bg-gray-800 rounded-lg text-white text-base"></button>
        </div>
    </div>

    <!-- MENU BUTTONS ON MAP  -->
    <div class="absolute top-0 mt-16 ml-1">
        <div class="flex flex-col">

            <!-- Search button -->
            <div class="mt-2 relative">
                <button class="map-btn" type="button" on:click={()=> searchField = !searchField ? true : false}>
                    <img src="./icons/search.png" alt="targetLocation">
                </button>
                {#if searchField}
                    <div transition:fly="{{x: -300, duration: 1000 }}" class="fixed ml-12 -mt-10 bg-white rounded shadow-lg flex flex-row">
                        <div>
                            <input class="p-1 w-full rounded outline-none" type="text" placeholder="" bind:value={searchVal}>
                        </div>
                        <div>
                            <button class="p-1 w-full text-white bg-gray-800 rounded" type="button" on:click={searchClicked}>  </button>
                        </div>
                    </div>
                {/if}
            </div>

            <!-- Fullscreen button -->
            <div class="mt-2">
                <button class="p-2 w-10 h-10 bg-white rounded shadow-xl" type="button" on:click={()=> toggleFullscreen('main')}>
                    <img src="./icons/fullscreen.png" alt="fullscreen">
                </button>
            </div>

            <!-- Location target button -->
            <div class="mt-2">
                <button class="map-btn" type="button" on:click={watchUserLocation}>
                    <img src="./icons/target.png" alt="targetLocation">
                </button>
            </div>

            <!-- Get taxi location button -->
            <div class="mt-2">
                <button class="map-btn" type="button" on:click={getTaxiLocation}>
                    <img src="./icons/taxi.png" alt="taxiLocation">
                </button>
            </div>

            <!-- Get taxi orders button -->
            <div class="mt-2">
                <button class="map-btn" type="button" on:click={taxiDriverCheck}>
                    <img src="./icons/taxi-marker.png" alt="taxiOrders">
                </button>
            </div>

            <!-- Chat button -->
            <div class="mt-2">
                <button class="map-btn" type="button" on:click={openChat}>
                    <img src="./icons/chat-4.png" alt="chat">
                </button>
            </div>
        </div>
    </div>

    <!-- TAXI ORDERS -->
    <div class=" absolute top-0 mt-16">
        {#if taxiOrdersBox}
            {#if userLocation != undefined && userLocation.region != undefined}
                <TaxiOrders region={userLocation.region} city={userLocation.city} latlng={userLocation.latlng} 
                on:lookOrderPlace={flyTo} on:driverArrived={checkDriverArrived}/>
            {/if}
        {/if}
    </div>

    <!-- CALL TAXI DIALOG -->
    <div class=" absolute top-0 mt-16">
        {#if callTaxiDialog}
            
            <TaxiCallDialog address={clickedAddress} {LatLng} clientUID={getUser.uid} clientName={getUser.displayName}
            region={userLocation.region} city={userLocation.city} 
            on:closeDialog={() => callTaxiDialog = false}/>
            
        {/if}
    </div>

    <!-- MY TAXI BOX -->
    <div class=" absolute top-0 mt-16">
        {#if myTaxiBox}
            {#if userLocation != undefined && userLocation.region != undefined}
                <TaxiClient region={userLocation.region} city={userLocation.city} on:findDriverLocation={flyTo}/>
            {/if}
        {/if}
    </div>

    <!-- CHAT BOX -->
    <div id="Chat" class=" absolute top-0 mt-16">
        {#if chatBox}
            {#if userLocation != undefined && userLocation.region != undefined}
                <Chat region={userLocation.region} city={userLocation.city} 
                on:fullscreen={()=> toggleFullscreen('Chat')}/>
            {/if}
        {/if}
    </div>

    <!-- ALERT BOX -->
    <!-- <div class=" absolute top-0 mt-32 ml-16">
        {#if alertBox}
        <div transition:fade="{{ duration: 500 }}" class="p-3 bg-gray-200 rounded-lg shadow-lg">
            <p>
                {alertBoxContent}
            </p>
            <p class="mt-2 text-center">
                <button on:click={() => alertBox = false}>Ok</button>
            </p>
        </div>
        {/if}
    </div> -->
</div>

<!-- ALERT BOX -->
    {#if alertBox}
        <div id="alertBox" class=" fixed top-0">

            <div transition:fade="{{ duration: 500 }}" class="p-3 border bg-white rounded-lg shadow-lg">

                <p class="text-center">
                    {alertBoxContent}
                </p>

                <p class="mt-2 text-center">
                    <button class="btn-orange" on:click={() => alertBox = false}></button>
                </p>

            </div>

        </div>
    {/if}


<style>#alertBox {
  width: 240px;
  height: 140px;
  left: 50%;
  top: 50%;
  margin-left:-120px;
  margin-top:-70px;
}

.map-btn {
  padding: 0.25rem;
  width: 2.5rem;
  height: 2.5rem;
  background-color: #fff;
  border-radius: 0.25rem;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.contextMenuVisible {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
}

.contextMenuDialogVisible {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
}

.callTaxiDialog {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
}

