<script>
    import { onMount } from 'svelte';
    import {firebase, db} from './../firebase.js';
    import {FirebaseApp, User, Doc, Collection} from 'sveltefire';
    import ethers from 'ethers';
    import { fade, fly } from 'svelte/transition';
    import tokenABI from './../tokenABI';
    import saveAs from 'file-saver';

    let user;

    let alertBox, alertBoxContent;
    let cryptoprogress, createWalletProgress, decryprtWalletProgress;
    let authTrue  = false;

    let wallet, defaultWallet, currentWallet, chooseWallet;
    let walletJSON, walletName, walletPassword, walletAddress, walletProvider, walletPrivateKey, walletMnemonic, walletPath;
    let tokenName, tokenAddress, tokenContract, showTokens, addTokenBlock;
    let tokenList = [];
    let walletList = [];
    let selectedWallet;
    let newWalletName, newNwalletPassword, addWalletBlock;

    let provider = ethers.getDefaultProvider();
    let currentNetwork = provider.network.name;

    // ETHERSCAN PROVIDER
    let etherscanProvider = new ethers.providers.EtherscanProvider();

    // AUTH
    function auth() {
        firebase.auth().onAuthStateChanged(function(usr) {
            if (usr) {
                user = usr;
                getUserWallets();
            } else {
                console.log('no user');
            }
        });
    }

    onMount(auth);

    // CURRENT WALLET
    function setCurrentWallet(w) {

        walletAddress = w.address;
        walletPrivateKey = w.privateKey;
        walletMnemonic = w.mnemonic;

        provider = ethers.getDefaultProvider();
        wallet = new ethers.Wallet(walletPrivateKey, provider);

        getWalletBalance()
        getGasPrice()
        getAddressTransactionsHistory(wallet.address)

    }

    // WALLET INFO
    let viewWalletMnemonic, viewWalletPrivateKey;
    let walletBalance;
    let gasPriceString;

    // IMPORT WALLET
    let importWalletPrivateKey;
    let importedWallet;

    // Operations
    // ---------------------------------------------------------------------------

    function getWalletBalance() {

        let balancePromise = wallet.getBalance();
    
        balancePromise.then((balance) => {
            let etherString = ethers.utils.formatEther(balance);
            walletBalance = etherString;
        });
    }

    function getWalletTransacionCount() {

        let transactionCountPromise = wallet.getTransactionCount();
    
        transactionCountPromise.then((transactionCount) => {
            console.log(transactionCount);
        });
    }

    function getGasPrice() {

        provider.getGasPrice().then((gasPrice) => {
            // gasPrice is a BigNumber; convert it to a decimal string
            gasPriceString = gasPrice.toString();
    
        });
    }

    // Getting the transaction history of an address
    function getAddressTransactionsHistory(a) {
        let address = a;
        let startBlock = 0;
        let endBlock = "latest";
        etherscanProvider.getHistory(address, startBlock, endBlock).then(function(history) {
            console.log(history);
            return history;
            // [
            //   {
            //     hash: '0x327632ccb6d7bb47b455383e936b2f14e6dc50dbefdc214870b446603b468675',
            //     blockHash: '0x0415f0d2741de45fb748166c7dc2aad9b3ff66bcf7d0a127f42a71d3e286c36d',
            //     blockNumber: 3135808,
            //     transactionIndex: 1,
            //     from: '0xb2682160c482eB985EC9F3e364eEc0a904C44C23',
            //     gasPrice: ethers.utils.bigNumberify('0x4a817c800'),
            //     gasLimit: ethers.utils.bigNumberify('0x493e0'),
            //     to: '0xAe572713CfE65cd7033774170F029B7219Ee7f70',
            //     value: ethers.utils.bigNumberify('0xd2f13f7789f0000'),
            //     nonce: 25,
            //     data: '0x',
            //     creates: null,
            //     chainId: 0
            //   },
            //   {
            //     hash: '0x7c10f2e7125a1fa5e37b54f5fac5465e8d594f89ff97916806ca56a5744812d9',
            //     ...
            //   }
            // ]
        });
    }

    // ALERT BOX
    function alertBoxCall(mes, t) {

        if (!alertBox) {

            alertBoxContent = mes;
            alertBox = true;
        }
        setTimeout(()=>{
            alertBox = false;
        }, t)
    }

    // ----------------------------------------------------------------------------

    // Network change
    function changeEthNetwork(e) {
 
        provider = ethers.getDefaultProvider(e.target.value);
        wallet = new ethers.Wallet(walletPrivateKey, provider);
        
        getWalletBalance()
        getGasPrice()
        getAddressTransactionsHistory(wallet.address)
    }

    // GET the balance of an account
    function getBalanceOfAnAccount(address) {

        provider.getBalance(address).then((balance) => {

            // balance is a BigNumber (in wei); format is as a sting (in ether)
            let etherString = ethers.utils.formatEther(balance);

            console.log("Balance: " + etherString);
        });
    }

    // Copy private key to clipboard
    function copyPrivateKeyToClipboard() {
        
        let copyText = document.getElementById("private-key");

        copyText.select();
        copyText.setSelectionRange(0, 99999); /*For mobile devices*/

        document.execCommand("copy");

        alertBoxCall('.', 2000);
    }

    // copy to clipboard
    function copyToClipboard(id) {
        
        let copyText = document.getElementById(id);

        copyText.select();
        copyText.setSelectionRange(0, 99999); /*For mobile devices*/

        document.execCommand("copy");

        alertBoxCall('.', 2000);
    }

    // create Password
    function createWalletPassword() {
        if (walletPassword == undefined || null) {
            alert(' !')
        }

        let password = walletPassword;

        function callback(progress) {
            cryptoprogress = parseInt(progress * 100)
            console.log("Encrypting: " + parseInt(progress * 100) + "% complete");
        }


        let encryptPromise = wallet.encrypt(password, callback);

        encryptPromise.then(function(json) {
            walletJSON = json;
            console.log(walletJSON);
            cryptoprogress = null;
        });

    }

    // Download JSON wallet
    function downloadJSONWallet() {

        let curwallet = walletList.filter(el => el.address === wallet.address);
        let data = curwallet[0].wjson;

        let fileName = `${curwallet[0].name}.json`;

        // Create a blob of the data JSON.stringify(data)
        let fileToSave = new Blob([data], {
            type: 'application/json',
            name: fileName
        });

        // Save the file
        saveAs(fileToSave, fileName);
    }

    // Import JSON wallet
    function importWalletJSON() {

        function callback(progress) {
            cryptoprogress = parseInt(progress * 100)
        }

        importedWallet = new ethers.Wallet.fromEncryptedJson(walletJSON, walletPassword, callback);
        setCurrentWallet(importedWallet);
    }

    // Import wallet
    function importWallet() {
        const importWallet = new ethers.Wallet(importWalletPrivateKey)
        setCurrentWallet(importWallet);
        alertBoxCall('', 2000);
    }

    // Add imported wallet
    function addImportedWallet(uid) {
        if (newWalletName == undefined || null) {
            alertBoxCall('  !', 5000)
        }
        else if (newNwalletPassword == undefined || null) {
            alertBoxCall('   !', 5000)
        }
        else if (importWalletPrivateKey == undefined || null) {
            alertBoxCall('   !', 5000)
        } 
        
        else {

        const importWallet = new ethers.Wallet(importWalletPrivateKey)

        setCurrentWallet(importWallet);
        walletName = newWalletName;

        const password = walletPassword;

        function callback(progress) {
            createWalletProgress = parseInt(progress * 100);
        }

        const encryptPromise = wallet.encrypt(password, callback);

        encryptPromise.then(function(json) {
            walletJSON = json;
            addWalletToAccount();
            createWalletProgress = null;
        });

        function addWalletToAccount() {
            db.collection(`users/${uid}/wallets`).doc(newWalletName).set({
                name: newWalletName,
                address: walletAddress,
                owner: uid,
                wjson: walletJSON
            })
            .then(function(docRef) {
                addWalletBlock = false;
                alertBoxCall(' .', 2000);
            })
            .catch(function(error) {
                console.error("Error adding document: ", error);
                alertBoxCall(`: ${error.message}`, 2000);
            });
        }
        }
    }

    // Get user wallets
    function getUserWallets() {

        db.collection(`users/${user.uid}/wallets`)
        .get().then(function(querySnapshot) {
            querySnapshot.forEach(function(doc) {
                
                const d = doc.data();
                walletList.push({
                    id: walletList.length,
                    name: d.name,
                    address: d.address,
                    wjson: d.wjson
                })
            });
            console.log(walletList)
        });
    }

    // Open selected wallet
    function openSelectedWallet() {

        const wallet = walletList[selectedWallet];
        walletName = wallet.name;

        function callback(progress) {
            decryprtWalletProgress = parseInt(progress * 100)
        }

        const openWallet = new ethers.Wallet.fromEncryptedJson(wallet.wjson, walletPassword, callback)
        
        openWallet.then((response)=> {
            
            setCurrentWallet(response)
            authTrue = true;
        })
        .catch((err)=> {
            console.log('error', err.message)
        })
    }

    // Open default wallet
    function openDefaultWallet() {

        function callback(progress) {
            decryprtWalletProgress = parseInt(progress * 100)
        }

        const defaultWallet = new ethers.Wallet.fromEncryptedJson(walletJSON, walletPassword, callback)
        
        defaultWallet.then((response)=> {
            
            setCurrentWallet(response)
            authTrue = true;
        })
        .catch((err)=> {
            console.log('error', err.message)
        })
    }

    // Load default wallet
    function loadDefaultWallet(uid) {
        db.collection(`users/${uid}/wallets`).doc('default')
        .get()
        .then(function(doc) {
            if (doc.exists) {
                const data = doc.data();
                walletJSON = data.wjson;
                walletName = doc.id;
                
                openDefaultWallet();
            } else {
            
                alert("!   .");
            }
        }).catch(function(error) {
            console.log("Error getting document:", error);
        });
    }

    // Create default wallet
    function createDefaultWallet(uid) {
        if (walletPassword == undefined || null) {
            alert(' !')
        }

        const randomWallet = ethers.Wallet.createRandom();

        setCurrentWallet(randomWallet);

        const password = walletPassword;

        function callback(progress) {
            createWalletProgress = parseInt(progress * 100);
        }

        const encryptPromise = wallet.encrypt(password, callback);

        encryptPromise.then(function(json) {
            walletJSON = json;
            addWalletToAccount();
            createWalletProgress = null;
        });

        function addWalletToAccount() {
            db.collection(`users/${uid}/wallets`).doc('default').set({
                name: 'default',
                address: walletAddress,
                owner: uid,
                wjson: walletJSON
            })
            .then(function(docRef) {
                alertBoxCall(' .', 2000);
            })
            .catch(function(error) {
                console.error("Error adding document: ", error);
            });
        }
    }

    // Add custom TOKEN
    function addCustomToken() {

        if (tokenAddress && tokenName == undefined || null) {
            alertBoxCall('   .', 3000);
        } 

        else {

            db.collection(`users/${user.uid}/wallets/${walletName}/tokens`).add({
                network: provider.network.name,
                name: tokenName,
                address: tokenAddress
            })
            .then(function(docRef) {
                alertBoxCall(' .', 3000);
                addTokenBlock = false;
            })
            .catch(function(error) {
                console.error("Error adding document: ", error);
            });
        }

    }

    function getWalletTokens() {

        showTokens = false;
        tokenList.length = 0;

        db.collection(`users/${user.uid}/wallets/${walletName}/tokens`)
        .where("network", "==", provider.network.name)
        .get()
        .then(function(querySnapshot) {
            querySnapshot.forEach(function(doc) {
                
                const data = doc.data();
                tokenList.push({
                    name: data.name,
                    address: data.address,
                    balance: 0,
                    id: doc.id
                });
            });
            getTokensBalance()
        });
    }

    function getTokensBalance() {

        tokenList.forEach(async(tok) => {
            
            const contract = new ethers.Contract(tok.address, tokenABI, provider);
            const balance = await contract.balanceOf(wallet.address);
            tok.balance = await balance.toString();
            showTokens = true;
        });
        console.log('tokenList', tokenList)
    }

    async function getTokenBalance(tokAdrr) {

        const contract = new ethers.Contract(tokAdrr, tokenABI, provider);
        const balance = await contract.balanceOf(wallet.address);
        
        console.log('balance string', balance.toString());
    }

    function createNewWallet(uid) {

        if (newWalletName == undefined || null) {
            alertBoxCall('  !', 2000)
        }
        else if (newNwalletPassword == undefined || null) {
            alertBoxCall('   !', 2000)
        }

        else {

            const randomWallet = ethers.Wallet.createRandom();
    
            setCurrentWallet(randomWallet);
    
            const password = walletPassword;
    
            function callback(progress) {
                createWalletProgress = parseInt(progress * 100);
            }
    
            const encryptPromise = wallet.encrypt(password, callback);
    
            encryptPromise.then(function(json) {
                walletJSON = json;
                addWalletToAccount();
                createWalletProgress = null;
            })
            .catch(function(error) {
                alertBoxCall(`: ${error.message}`, 5000);;
            });

            function addWalletToAccount() {
                db.collection(`users/${uid}/wallets`).doc(newWalletName).set({
                    name: newWalletName,
                    address: walletAddress,
                    owner: uid,
                    wjson: walletJSON
                })
                .then(function(docRef) {
                    alertBoxCall(' .', 2000);
                })
                .catch(function(error) {
                    alertBoxCall(`: ${error.message}`, 5000);;
                });
            }

        }

    }

    let sendAmount;
    let sendTo;
    let selectedToken;
    let txHash;
    let sendEtherBlock;

    function sendToken() {

        const tokenContract = new ethers.Contract(selectedToken, tokenABI, wallet);

        // How many tokens?
        const numberOfDecimals = 18;
        const numberOfTokens = ethers.utils.parseUnits(sendAmount, numberOfDecimals);

        // Send tokens
        tokenContract.transfer(sendTo, numberOfTokens).then(function(tx) {
            console.log(tx);
            txHash = tx.hash;
            alertBoxCall('', 3000)
        })
        .catch((err) => {
            alertBoxCall(`: ${err.message}`, 3000)
        })
    }

    function sendEther() {

        let amount = ethers.utils.parseEther(sendAmount);

        let tx = {
            to: sendTo,
            // ... or supports ENS names
            // to: "ricmoo.firefly.eth",

            // We must pass in the amount as wei (1 ether = 1e18 wei), so we
            // use this convenience function to convert ether to wei.
            value: amount
        };

        const sendPromise = wallet.sendTransaction(tx);

        sendPromise.then((tx) => {
            console.log(tx);
            // {
            //    // All transaction fields will be present
            //    "nonce", "gasLimit", "pasPrice", "to", "value", "data",
            //    "from", "hash", "r", "s", "v"
            // }
            alertBoxCall('', 3000)
        })
        .catch((err) => {
            alertBoxCall(`: ${err.message}`, 3000)
        })
    }



</script>

<div class="main-container">
    <FirebaseApp {firebase}>
        <User let:user>

            <Doc path={`users/${user.uid}/wallets/default`} let:data={wallet} let:ref>

                    <!-- LOADING -->
                    <div slot="loading" class="mt-16 text-white text-center">
                        ...
                    </div>
                    
                    <!-- NO WALLET -->
                    <div slot="fallback">
                        
                        <!-- WELCOME -->
                        <div class="welcome-container p-2 bg-gray-900 h-auto">
                            <div class="welcome-block p-2 bg-gray-100 rounded-lg">

                                <div class="text-justify leading-tight text-sm">

                                    <p class=" font-semibold text-center text-base">
                                     !
                                    </p>
                                    <p class=" mt-2">
                                              -   .
                                           !        "".
                                    </p>
                                    <p class="mt-1 text-red-600">
                                        <b>!</b>       ,    ,   
                                          ,       .     
                                            .
                                    </p>
                                    <p class="mt-1 text-teal-600">
                                              ,        .
                                               ,      .
                                               ( JSON)    ,    
                                          .      : "  ", " JSON-".
                                    </p>
                                </div>
                                <div>
                                    {#if createWalletProgress != undefined || null}
                                        <progress class="mt-2 w-full rounded-full overflow-hidden" max="100" value={createWalletProgress}></progress>
                                    {/if}
                                </div>
                                <div class="mt-3 text-center">
                                    
                                    <input class="input-password" type="password" placeholder="" bind:value={walletPassword}>
                                    
                                </div>
                                <div class="mt-3 text-center">
                                    
                                    <button class="btn-orange" on:click|once={()=> createDefaultWallet(user.uid)}>  </button>
                                    
                                </div>
                            </div>
                        </div>
                    </div>

                    <!-- WALLET EXIST -->
                    <div class="wallet-container p-2 bg-gray-900 h-auto">

                        <!-- Sign Out -->
                        {#if !authTrue}
                        <div class="auth-block p-4 bg-gray-100 rounded-lg">

                            <p class="mb-2 font-semibold text-center text-gray-800">
                                 :
                            </p>

                            {#if walletList.length > 1}
                                <div>
                                    {#each walletList as w}

                                            <div class="mt-1 p-1 text-orange-600 bg-white border rounded-lg">
                                                
                                                <label for="">
                                                    <span>
                                                        {w.name} 
                                                    </span>
                                                    <input type="radio" bind:group={selectedWallet} value={w.id}>
                                                </label>

                                                <span class=" text-teal-600 text-xs tracking-tight">
                                                    {w.address}
                                                </span>
                                            </div>
                                    {/each}
                                </div>
                            {:else}
                                <div class="mt-1 p-1 text-orange-600 bg-white border rounded-lg">
                                    
                                    <label for="">
                                        <span>
                                            {walletList[0].name} 
                                        </span>
                                            <input type="radio" bind:group={selectedWallet} value={0} checked>
                                    </label>

                                    <span class=" text-teal-600 text-xs tracking-tight">
                                        {walletList[0].address}
                                    </span>
                                </div>
                            {/if}

                            <div class="mt-3">
                                <p class=" text-center font-semibold text-gray-700">
                                      :
                                </p>
                            </div>
                            <div>
                                {#if decryprtWalletProgress != undefined || null}
                                    <progress class="mt-4 w-full rounded-full overflow-hidden" max="100" value={decryprtWalletProgress}></progress>
                                {/if}
                            </div>
                            <div class="mt-2 text-center">
                                <input class="input-password" type="password" bind:value={walletPassword}>
                            </div>
                            <div class="mt-4 text-center">
                                <button class="btn-orange" on:click={()=> openSelectedWallet()}>  </button>
                            </div>
                        </div>

                        <!-- Sign In -->
                        {:else}
                        <div class="wallet flex flex-col px-2 rounded-lg bg-gray-200">
            
                            <!-- HEADER -->
                            <div class="wallet-header">
                            </div>

                            <!-- NETWORK -->
                            <div class="wallet-block">

                                <div class=" flex flex-row justify-between items-center">
                                    <div>
                                        <span class="text-sm text-gray-700 font-semibold">
                                             : 
                                        </span>

                                        <!-- help -->
                                        <span class="help">
                                            <img src="./icons/help.svg" alt="help" class="w-4 h-4">
                                            <span>
                                                "Homestead" -    (Ethereum mainnet).
                                                "Ropsten" -    (Ropsten testnet).
                                                "Rinkeby" -    (Rinkeby mainnet).
                                            </span>
                                        </span>
                                    </div>
                                    <div>
                                        <span>
                                            <select id="select-network" on:input={changeEthNetwork}>
                                                <option value="homestead">homestead</option>
                                                <option value="rinkeby">rinkeby</option>
                                                <option value="ropsten">ropsten</option>
                                            </select>
                                        </span>
                                    </div>
                                </div>
                            </div>

                            <!-- ADD NEW WALLET -->
                            {#if addWalletBlock}
                            <div class="wallet-block">

                                <div class=" flex flex-col">

                                    <div>
                                        <p class=" text-sm text-center text-orange-600 font-semibold">
                                              :
                                        </p>
                                        {#if createWalletProgress != undefined || null}
                                            <progress max="100" class=" mt-2 w-full rounded-full overflow-hidden" value={createWalletProgress}/>
                                        {/if}
                                        <p class="mt-2">
                                            <input class="input-field" type="text" placeholder=" " bind:value={newWalletName}>
                                            <button class="btn-gray ml-1" on:click={()=> addWalletBlock = false}></button>
                                        </p>
                                        <p class="mt-1">
                                            <input class="input-field" type="text" placeholder=" " bind:value={newNwalletPassword}>
                                            <button class="btn-orange" on:click={()=> createNewWallet(user.uid)}></button>
                                        </p>
                                    </div>

                                    <div class="mt-2">
                                        <p class=" text-sm text-center text-orange-600 font-semibold">
                                                :
                                        </p>
                                        <p>
                                            <input class="input-field" type="text" placeholder=" " bind:value={importWalletPrivateKey}>
                                            <button class="btn-green" on:click={()=> addImportedWallet(user.uid)}></button>
                                        </p>
                                    </div>

                                    <div class="mt-2">
                                        <p class=" text-sm text-center text-orange-600 font-semibold">
                                             :
                                        </p>
                                        <p>
                                            <input class="input-field" type="text" placeholder=" " bind:value={importWalletPrivateKey}>
                                            <button class="btn-orange" on:click={importWallet}></button>
                                        </p>
                                    </div>
                                </div>
                                
                            </div>
                            {/if}

                            <!-- CURRENT WALLET -->
                            <div class="wallet-block">

                                <div>
                                    <div>
                                        <span class="text-sm text-gray-700 font-semibold">
                                             : 
                                        </span>
                                    </div>
                                    <div class="flex flex-row justify-between items-center">
                                        <div class=" flex flex-row">
                                            <div class="p-1 w-full border border-gray-400 bg-white rounded-lg">
                                                {walletName}
                                            </div>
                                            <div class="ml-1 p-1 rounded-full bg-teal-500 shadow-md">
                                                <img src="./icons/unfold_more.svg" alt="copy" on:click={()=> authTrue = false} class="w-6 h-6 cursor-pointer">
                                            </div>
                                        </div>
                                        <div>
                                            <button class="btn-opt mb-2" on:click={()=> addWalletBlock = true}>
                                             +
                                            </button>
                                        </div>
                                    </div>

                                    <!-- Address -->
                                    <div class="mt-1 flex flex-row justify-between items-center">

                                        <div class=" w-11/12">
                                            <input id="wallet-address" type="text" value={walletAddress}>
                                        </div>

                                        <div class="px-1">
                                            <img src="./icons/file-copy.svg" alt="copy" on:click={()=> copyToClipboard('wallet-address')} class=" w-6 h-6 cursor-pointer">
                                        </div>

                                    </div>

                                    <div>
                                        <span class="link-wallet-secret" on:click={()=> viewWalletPrivateKey = true}>
                                              
                                        </span>
                                        <span class="link-wallet-secret" on:click={()=> downloadJSONWallet()}>
                                            JSON-
                                        </span>
                                    </div>
                                </div>

                                <!-- view private key -->
                                {#if viewWalletPrivateKey}
                                <div class="wallet-secret">

                                    <div class="mt-2">
                                        
                                        <input id="private-key" class="view-private-key" type="text" value={walletPrivateKey} on:click={copyPrivateKeyToClipboard}>
                                        
                                    </div>
                                        
                                    <div class="mt-2">
                                        <button class="btn-gray" on:click={()=> viewWalletPrivateKey = false}>  </button>
                                    </div>
                                    
                                </div>
                                {/if}

                            </div>

                            <!-- WALLET BALANCE -->
                            <div class="wallet-block">

                                <div class=" flex flex-col">

                                    <div>
                                        <p class=" text-center font-semibold text-green-500">
                                            
                                        </p>
                                    </div>

                                    <div class="mt-2 flex flex-col">

                                        <!-- ETHEREUM -->
                                        <div class="token">
                                            <div class=" flex flex-row justify-between items-center">

                                                <div class="ml-1 font-semibold text-blue-500">
                                                    Eth: 
                                                </div>

                                                <div class=" text-orange-500">
                                                    {walletBalance} 
                                                </div>

                                                <div >
                                                    {#if !sendEtherBlock}
                                                        <img class="w-8 h-8 cursor-pointer" src="./icons/expand_more.svg" alt="more" 
                                                        on:click={()=> sendEtherBlock = true}>
                                                    {:else}
                                                        <img class="w-8 h-8 cursor-pointer" src="./icons/expand_less.svg" alt="less" 
                                                        on:click={()=> sendEtherBlock = false}>
                                                    {/if}
                                                </div>

                                            </div>
                                            {#if sendEtherBlock}
                                                <div class="text-center">
                                                    <input class="input-field" type="text" placeholder=" " bind:value={sendTo}>
                                                    <input class="input-field" type="text" placeholder="" bind:value={sendAmount}>
                                                    <button class="btn-green" on:click={sendEther}>  </button>
                                                </div>
                                            {/if}
                                        </div>
                                        
                                        <!-- RUBL -->
                                        <div class="token">
                                            <p>
                                                <span class=" text-blue-500">
                                                    Py6: 
                                                </span>

                                                <span class=" text-orange-500">
                                                    {walletBalance} 
                                                </span>
                                            </p>
                                        </div>

                                        <!-- GAS PRICE -->
                                        <div class="token">
                                            <p>
                                                <span class=" text-blue-500">
                                                     : 
                                                </span>

                                                <span class=" text-orange-500">
                                                    {Math.trunc(+gasPriceString / 1000000000)} gwei 
                                                </span>
                                            </p>
                                        </div>
                                    </div>

                                    <div class="mt-2 text-center">
                                        <button class="btn-opt" on:click={getWalletTokens}> </button>
                                        <button class="btn-opt" on:click={()=> addTokenBlock = true}> </button>
                                    </div>
                                </div>

                                <!-- TOKENS -->
                                {#if showTokens}
                                    <div class="mt-3 flex flex-col">
                                        {#each tokenList as token}
                                            
                                            <div class="token">
                                                <label>
                                                    <span class=" text-blue-500">
                                                        {token.name}:
                                                    </span>
                                                    <span class=" text-orange-500">
                                                        {+token.balance / 1e18}
                                                    </span>
                                                    <input type="radio" bind:group={selectedToken} value={token.address}>
                                                </label>
                                            </div>
                                        {/each}
                                    </div>
                                {/if}
                            </div>

                            <!-- ADD TOKEN -->
                            {#if addTokenBlock}
                            <div class="wallet-block">
                                <div>
                                    <p class=" text-center font-semibold text-teal-500">
                                         :
                                    </p>
                                </div>
                                <div class=" text-center">
                                    <input class="input-field" type="text" placeholder=" " bind:value={tokenName}>
                                    <input class="input-field" type="text" placeholder=" - " bind:value={tokenAddress}>
                                </div>
                                <div class=" text-center">
                                    <button class="btn-orange" on:click={addCustomToken}>  </button>
                                    <button class="btn-gray" on:click={()=> addTokenBlock = false}>  </button>
                                </div>
                            </div>
                            {/if}

                            <!-- TRANSFER TOKEN-->
                            {#if selectedToken}
                            <div class="wallet-block">
                                <div>
                                    <p class=" text-center font-semibold text-teal-500">
                                         
                                    </p>
                                    
                                    <div class=" text-center">
                                        <input class="input-field" type="text" placeholder=" " bind:value={sendTo}>
                                        <input class="input-field" type="text" placeholder="" bind:value={sendAmount}>
                                        <button class="btn-green" on:click={sendToken}>  </button>
                                    </div>
                                    {#if txHash}
                                        <div class="mt-1 p-1 w-full bg-white rounded-lg truncate">
                                            <a class="link-hash" href={`https://${provider.network.name}.etherscan.io/tx/${txHash}`}> 
                                            {txHash} 
                                            </a>
                                        </div>
                                    {/if}
                                </div>
                            </div>
                            {/if}

                            <!-- WALLET HISTORY -->
                            <!-- <div class="wallet-block">
                                {#await getTransactionHistory}
                                <p>...</p>
                                {:then history}
                                    {#each history as h}
                                        <div>
                                            <p>{h.hash}</p>
                                            <p>{h.from}</p>
                                            <p>{h.to}</p>
                                            
                                        </div>
                                    {/each}
                                {:catch err}
                                    <p>{err.message}</p>
                                {/await}
                            </div> -->

                        </div>
                        {/if}
                    </div>

                
            </Doc>

        </User>
    </FirebaseApp>
</div>

<!-- ALERT BOX -->
    {#if alertBox}
        <div id="alertBox" class=" fixed top-0">

            <div transition:fade="{{ duration: 500 }}" class="p-3 border bg-white rounded-lg shadow-lg">

                <p class="text-center">
                    {alertBoxContent}
                </p>

                <p class="mt-2 text-center">
                    <button class="btn-orange" on:click={() => alertBox = false}></button>
                </p>

            </div>

        </div>
    {/if}



