<script>
    import {firebase, db, googleProvider} from './../firebase.js';
    import firebaseui from 'firebaseui';
    import {FirebaseApp, User, Doc, Collection} from 'sveltefire';
    import {push} from 'svelte-spa-router';
    import { fade, fly } from 'svelte/transition';

    let isOpen = false;
    let email = '';
    let password = '';
    let username = '';
    let promocode = '';
    let promoRef;
    let promocodeExist = false;
    let usernameExist = '    6 .';
    let newbalance;

    let ui = new firebaseui.auth.AuthUI(firebase.auth());

    // AUTH USER and DB
    let getUser;
    let getUserAccount;

    firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
            // User is signed in.
            getUser = user;
        } else {
            // No user is signed in.
            console.log('no user login')
        }
    });

    function authUI() {

        ui.start('#firebaseui-auth-container', {
            callbacks: {
                signInSuccessWithAuthResult: function(authResult, redirectUrl) {
                // User successfully signed in.
                // Return type determines whether we continue the redirect automatically
                // or whether we leave that to developer to handle.
                return true;
                },
                uiShown: function() {
                // The widget is rendered.
                // Hide the loader.
                document.getElementById('loader').style.display = 'none';
                }
            },
            // signInFlow: 'popup',
            // signInSuccessUrl: '<url-to-redirect-to-on-success>',
            signInOptions: [
                // List of OAuth providers supported.
                firebase.auth.EmailAuthProvider.PROVIDER_ID,
                firebase.auth.PhoneAuthProvider.PROVIDER_ID,
                firebase.auth.GoogleAuthProvider.PROVIDER_ID
            ],
            // Other config options...
        });
    }

    function activatePromocode(code, uid) {
        let promoRef = db.collection('admin').doc('promo');
        let opt = {
            source: 'server'
        };
        promoRef.get(opt).then(function(doc) {
            if(doc.exists) {
                let data = doc.data();
                if(code in data.active) {
                    let userRef = db.collection('users').doc(uid);
                    userRef.update({
                        balance: data.active[code]
                    })
                    console.log('promocode activated!')
                    console.log('new balance: ', data.active[code])
                } else {
                    console.log('promocode not found')
                }
                console.log('doc.data', data)
                console.log('doc.active', data.active)
            } else {
                console.log('some error')
            }
        }).catch(function(error) {
            console.log("Error getting document:", error);
        });
    }

    function usernameCheck() {
        if (username.length >= 6) {

            usernameExist = '  .';

            db.collection('users').where('username','==', username)
            .get()
            .then(function(querySnapshot) {
                querySnapshot.forEach(function(doc) {
                    // doc.data() is never undefined for query doc snapshots
                    // console.log(doc.id, " => ", doc.data());
                    usernameExist = '  ,  .'
                });
            })
            .catch(function(error) {
                console.log("Error getting documents: ", error);
            });
        } else {
            usernameExist = '    6 .';
        }
    }

    function promocodeCheck() {
        if (promocode.length >= 6) {

            promocodeExist = false;

            db.collection('users').where('username','==', promocode)
            .get()
            .then(function(querySnapshot) {
                querySnapshot.forEach(function(doc) {
                    promoRef = doc.id;
                    promocodeExist = true;
                });
            })
            .catch(function(error) {
                console.log("Error getting documents: ", error);
            });
        } else {
            promocodeExist = false;
        }
    }

    function completeRegistration() {
        if (username.length  && promocode.length >= 6) {
            
            if (promocodeExist) {
                
                addNewUser()
                alert('   .   ,    .')
                push('/about')
                isOpen = false;
                  
            } else {
                alert(' !')
            }

        } else {
            
            alert('     .')
        }
    }

    // Cloud Functions
    let addNewUserFunction = firebase.functions().httpsCallable('addNewUser');

    // Call cloud functions
    function addNewUser() {

        addNewUserFunction({referrer: promoRef, username: username}).then(function(result) {
            console.log('registration', result)
        }).catch(function(error) {
            console.log(error.code, error.message, error.details)
        });
    }

</script>

<div class="header fixed z-10 h-16 top-0 w-full bg-gray-900">

    <!-- Logo -->
    <div class="justify-center flex flex-row items-center">
        <div class="mt-2 -ml-6">    
            <img class="w-12 h-12" src="./icons/main-logo.png" alt="logo">
        </div>
        <div class="ml-2 mt-1">
            <span class=" text-red-600 text-xl font-semibold">Avto</span><span class=" text-green-500 text-xl font-semibold">karta</span><span class=" text-orange-500 text-xl font-semibold">.club</span>
        </div>
    </div>

    <!-- BURGER BUTTON -->
    <div class="header-menu absolute top-0 right-0 p-2 text-center">
        {#if !isOpen}
            <img src="./icons/menu.svg" alt="menu" on:click={() => isOpen = true} class="mt-1 mr-1 w-10 h-10 cursor-pointer">
        {:else}
            <img src="./icons/close.svg" alt="menu" on:click={() => isOpen = false} class="mt-1 mr-1 w-10 h-10 cursor-pointer">
        {/if}
    </div>

    <!-- MENU -->
    {#if isOpen}
    <div transition:fade="{{ duration: 200 }}" class="menu-container">
        
        <div class="user-auth">
            <FirebaseApp {firebase}>
                <User let:user let:auth>
                    <!-- User info -->
                    <div class="user-info">
                        <!-- <p> {user.email} </p> -->
                        <p> {user.displayName} </p>
                    </div>
                    <!-- Account info -->
                    <div class="account-info">
                        <Doc path={`users/${user.uid}`} let:data>

                            <div slot="loading">
                                <p>...</p>
                            </div>

                            {#if data != undefined}
                            <div>
                                
                                <p>: {data.username}</p>
                                <p>. : {user.email}</p>
                                <p>: {data.balance}</p>
                                <p>: {data.mapRating}</p>
                                
                            </div>
                            {/if}
                            <div slot="fallback">
                                <div class="mt-2 p-1 bg-gray-300 text-teal-900">
                                    <p class="my-2 font-semibold text-red-600 text-lg">  : </p>
                                    <p class=" text-sm">        . </p>
                                    <input class="menu-input mt-2" type="text" placeholder=" " bind:value={username} on:input={usernameCheck}>
                                    <input class="menu-input" type="text" placeholder="" bind:value={promocode} on:input={promocodeCheck}>
                                    <span class=" text-sm block">{usernameExist}</span>
                                    <p>
                                        <button class="p-1 my-2 bg-teal-500 text-white rounded" on:click|once={completeRegistration}></button>
                                    </p>
                                </div>
                            </div>
                            
                        </Doc>
                    </div>
                    <div>
                        <button class="p-1 px-3 mt-6 bg-gray-800 text-gray-200 rounded" on:click={()=> auth.signOut()}></button>
                    </div>
                    <!-- Signed out -->
                    <div slot="signed-out">
                        <div id="loader">
                            <button class="btn-sign" on:click={authUI}></button>
                        </div>
                            <div id="firebaseui-auth-container"></div>
                    </div>
                </User>
            </FirebaseApp>
        </div>
        <br>
        <hr>

        <!-- MENU LIST -->
        <ul class="menu-list mt-5" on:click={ () => isOpen = false }>
            <li><a href="/"></a></li>
            <li><a href="#/about"></a></li>
            <li><a href="#/cryptowallet">-</a></li>
            <li><a href="#/messages"></a></li>
            <li><a href={"#/profile"}></a></li>
        </ul>
    </div>
    {/if}
</div>

<style>.btn-sign {
  margin-top: 0.75rem;
  padding: 0.5rem;
  background-color: #dd6b20;
  color: #fff;
}

.btn-sign:hover {
  background-color: #ed8936;
  color: #fff;
}

.header-menu-icon {
  height: 3rem;
  width: 3rem;
  fill: currentColor;
  color: #61d4b3;
  cursor: pointer;
}

.menu-container {
  z-index: 50;
  padding: 0.5rem;
  width: 100%;
  height: 100vh;
  background-color: #1a202c;
  color: #61d4b3;
  text-align: center;
}

.menu-list li {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  font-size: 1.125rem;
}

.menu-list li:hover {
  background-color: #2d3748;
}

.main-container {
  max-width: 100%;
}

@media (min-width: 1024px) {
  .menu-container {
    width: 25%;
    position: absolute;
    right: 0;
  }
}

